import React from "react";
import "./App-info.css";

const AppInfo = ({ totalUsers, totalIncreaseUsers }) => {
    return (
        <div className="app-info">
            <h1>    SUF</h1>
            <h2>  : {totalUsers}</h2>
            <h2> : {totalIncreaseUsers}</h2>
        </div>
    );
};

export default AppInfo;
